	module.exports = {"data":{"prismicDocument":{"data":{"feature_name":[{"type":"heading1","text":"Record Keeping"}],"feature_tagline":[{"type":"heading2","text":"All your documents and client records stored encrypted in one place"}],"sell":[{"type":"paragraph","text":"Keep track of everything that happens both within and outside CarePlanner. All securely stored and cloud-accessible."}],"feature_icon":{"url":"https://prismic-io.s3.amazonaws.com/careplanner%2F92636409-6690-40ee-96d9-8c846db0646f_record-keeping.svg"},"feature_image":{"url":null},"feature_body_copy":[{"type":"paragraph","text":"There are few other sectors where record keeping is as important as it is in social care, and CarePlanner has this covered. Using our cloud-based system, you can:"},{"type":"list-item","text":"Keep secure records of all events that occur, including complaints, compliments, assessments, reviews etc."},{"type":"list-item","text":"Keep track of service user documentation, including care plans and risk assessments"},{"type":"list-item","text":"Receive advance warning of when records will expire, or need to be updated"},{"type":"list-item","text":"Keep track of all staff documentation and legal requirements, including DBS checks, manual handling certificates and supervision requirements"},{"type":"list-item","text":"Track training requirements for all staff, receive warnings when training is out-of-date and run reports on training arrangements across your business"},{"type":"list-item","text":"Include important information on staff rotas based on records, ranging from warnings about medication changes through to reminders to wish a client a happy birthday!"},{"type":"list-item","text":"Encrypt keysafe numbers on rotas, so that only your staff can read them"},{"type":"list-item","text":"Upload any document to CarePlanner, and rest assured in the knowledge that it is safe, backed-up, and encrypted"},{"type":"heading2","text":"Recording Service User Information"},{"type":"paragraph","text":"Let's say you take on a new, privately-funded service user. You'll want to conduct an initial assessment and upload the results of that straight into the system. You'll want to book in a follow-up care needs assessment, and also a home risk assessment. You'll want to input the service user's personal details and care requirements, as well as a preliminary care schedule and any periods when they won't require care, making sure that appointments are cancelled during that time. With CarePlanner, this can all be accomplished with just a few clicks. All personal information entered into the system is immediately encrypted, and remains that way even in system backups. This is also the case for all documents that you upload."},{"type":"heading2","text":"Recording Correspondence"},{"type":"paragraph","text":"Any correspondence with a service user or their family can be immediately logged using the CarePlanner 'diary' system. This also allows you to schedule alerts for follow-ups, to flag information up to care staff and to assign tasks to members of your admin team."},{"type":"heading2","text":"Recording Care Worker Information"},{"type":"paragraph","text":"With CarePlanner you can set up a list of items that you need from a new starter, and choose which of those items are required before the member of staff can take on any solo appointments. You can upload their references, DBS checks and car insurance docs into the system, and enter their expiry dates at the same time. This will ensure that the system warns you in plenty of time to obtain renewals. You can then go ahead and schedule the initial training and shadowing, knowing that the system won't allow any of your admin staff to assign the new carer to an appointment unless copies of all the necessary records have been uploaded."}],"feature_bullets":[{"feature_bullet":[{"type":"paragraph","text":"Keep a record of all events that occur, including complaints, assessments, reviews etc."}]},{"feature_bullet":[{"type":"paragraph","text":"See a full audit trail of changes made in CarePlanner, including who made them"}]},{"feature_bullet":[{"type":"paragraph","text":"Record staff training dates and receive alerts when renewal is due"}]},{"feature_bullet":[{"type":"paragraph","text":"Keep all your documents together in one secure system"}]}],"features_sidebar":[{"feature_text":[]}]}}},"pathContext":{"permalink":"/features/record-keeping","prev":{"slugs":["customisation","configured-for-your-business"],"type":"feature","fields":{"permalink":"/features/customisation"},"data":{"feature_name":[{"type":"heading1","text":"Customisation"}],"feature_tagline":[{"type":"heading2","text":"Configured for your business"}],"sell":[{"type":"paragraph","text":"If CarePlanner doesn't do exactly what you want... just tell us. We're experts at making software work for people."}],"title":null,"excerpt":null,"feature_image":{"url":null},"key_stat_figure":null,"key_stat_text":null,"large_image":null,"summary":null,"company_name":null}},"next":{"slugs":["customisation","configured-for-your-business"],"type":"feature","fields":{"permalink":"/features/customisation"},"data":{"feature_name":[{"type":"heading1","text":"Customisation"}],"feature_tagline":[{"type":"heading2","text":"Configured for your business"}],"sell":[{"type":"paragraph","text":"If CarePlanner doesn't do exactly what you want... just tell us. We're experts at making software work for people."}],"title":null,"excerpt":null,"feature_image":{"url":null},"key_stat_figure":null,"key_stat_text":null,"large_image":null,"summary":null,"company_name":null}}}}

